*{margin:0;padding:0;box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-size:62.5%;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white;box-sizing:border-box;font-size:62.5%}.container{display:flex}.Sidebar{background-color:#3c3a41;height:100vh;width:20%;font-size:1.2rem;overflow:auto;overflow-x:hidden;transition:all .4s}.Sidebar__list{display:flex;flex-direction:column;width:100%}.Sidebar__list-item{padding:1rem 1.5rem;border-bottom:0.1rem solid #1f1d22;cursor:pointer;transition:all .8s}.Sidebar__list-item--active{padding:1rem 1.5rem;border-bottom:0.1rem solid #1f1d22;cursor:pointer;transition:all .8s;background-color:#9acd32}.Sidebar__list-item:hover{background-color:#9acd32}.Sidebar__link{color:#9acd32;background-color:#1f1d22}@media only screen and (max-width: 992px){.Sidebar{width:fit-content}}
