*{margin:0;padding:0;box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-size:62.5%;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white;box-sizing:border-box;font-size:62.5%}.container{display:flex}.Toggle{color:white;background-color:#1f1d22;position:relative}.Toggle__btn{display:inline-block;margin:1rem;text-decoration:none;color:#FFF;width:3.5rem;height:3.5rem;line-height:3.5rem;border-radius:50%;text-align:center;vertical-align:bottom;overflow:hidden;background-image:linear-gradient(45deg, #5e8516, rgba(166,252,69,0.972) 100%);transition:.4s;position:absolute;cursor:pointer}.Toggle__btn:hover{transform:translateY(0.5rem)}.test{margin-top:1.1rem}
